import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import detector from 'i18next-browser-languagedetector';
import moment from 'moment';

import translationEN from '@hubcitymedia/commons-scripts/src/locales/en/translation.js';

function capitalize(string) {
  // note(ML): This is REDUNDANT.  Function exists in jsUtils.
  // Capitalize the first letter of string.
  // Note: If an empty string is supplied, it will return an empty string.
  // Note: This is different than lodash's _.capitalize, which lowercases all the other letters.
  return string.charAt(0).toUpperCase() + string.slice(1);
}

function capitalizeAll(string) {
  // note(ML): This is REDUNDANT.  Function exists in jsUtils.
  // Capitalize the first letter of string and every first letter after a space.
  // For example, 'this is input' will become 'This Is Input'.
  return string.split(' ').map(capitalize).join(' ');
}

// the translations
const resources = {
  en: {
    translation: translationEN,
  },
  // you can add more locales here, such as:
  // it: {
  //   translation: translationIT,
  // },
};

i18n
// auto-detect locale base on http request
  .use(detector)
// passes i18n down to react-i18next
  .use(initReactI18next)
// keep moment locale in-sync w/ i18next locale
  .on('languageChanged', lng => moment.locale(lng))
  .init({
    // see https://www.i18next.com/overview/configuration-options
    resources,
    fallbackLng: 'en',
    // we do not use keys in form messages.welcome
    keySeparator: false,

    interpolation: {
      // react already escapes things
      escapeValue: false,
      format: function(value, format) {
        if (format === 'uppercase') {
          return value.toUpperCase();
        }
        else if (format === 'lowercase') {
          return value.toLowerCase();
        }
        else if (format === 'capitalize') {
          return capitalize(value);
        }
        else if (format === 'capitalizeAll') {
          return capitalizeAll(value);
        }
        else if (value instanceof Date) {
          return moment(value).format(format);
        }
        else {
          return value;
        }
      },
    },

    preload: ['en'],

    // if you want to log more output to console
    debug: false,

    // language detection options for i18next-browser-languagedetector
    detection: {
      // order and from where user language should be detected
      order: ['querystring', 'cookie', 'localStorage', 'navigator', 'htmlTag', 'path', 'subdomain'],

      // keys or params to lookup language from
      lookupQuerystring: 'lng',
      lookupCookie: 'i18next',
      lookupLocalStorage: 'i18nextLng',
      lookupFromPathIndex: 0,
      lookupFromSubdomainIndex: 0,

      // cache user language on
      caches: ['localStorage', 'cookie'],
      // languages to not persist (cookie, localStorage)
      excludeCacheFor: ['cimode'],

      // optional expire and domain for set cookie
      cookieMinutes: 10,
      cookieDomain: 'myDomain',

      // optional htmlTag with lang attribute, the default is:
      htmlTag: document.documentElement,
    },

  });

export default i18n;
