import _ from 'lodash';
import React from 'react';
import PropTypes from 'prop-types';
import { useTable } from 'react-table';
import { withStyles } from '@material-ui/core/styles';
import {
  Grid,
  Paper,
  FormControl,
  Input,
  InputLabel,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TablePagination,
  Toolbar,
  Typography,
} from '@material-ui/core';
import GlossarySearchInput from 'src/components/GlossarySearchInput/GlossarySearchInput.js';

import themeToStyles from './styles.js';

const renderHeader = (props, headerGroup) => {
  var is_filter_enabled = props.enableFilters;
  return (
    <TableRow {...headerGroup.getHeaderGroupProps()}>
      {headerGroup.headers.map((column, index) => {
        var column_name = column.Header;
        return (
          <TableCell {...{
            key: index,
            ...column.getHeaderProps(),
            className: props.classes.tableHeaderCell,
          }}>
            {is_filter_enabled  ? 
              <FormControl>
                <InputLabel htmlFor={column_name}>{column_name}</InputLabel>
                <Input
                  className={props.classes.tableHeaderInput}
                  id={column_name}
                  onChange={(event) => props.onFilterKeyDown(event, column_name)}
                />
              </FormControl>
              : column.render('Header')
            }
          </TableCell>
        );
      })}
    </TableRow>
  );
};

const renderTable = (props) => {
  // Render the UI for your table
  // Use the state and functions returned from useTable to build your UI
  const { getTableProps, headerGroups, rows, prepareRow } = useTable(props.reactTableProps);
  const { page, rowsPerPage, toolbarProps } = props;
  return (
    <Paper>
      <Grid container spacing={0} direction='row'>
        <Grid item xs={2}>
          <GlossarySearchInput />
        </Grid>
        <Grid item xs={10}>
          <Toolbar className={props.classes.tableToolbar}>
            <Grid container className={props.classes.tableToolbarContainer}>
              { toolbarProps.title && 
              <Grid item>
                <Typography className={props.classes.tableHeader} variant='h6'>
                  {toolbarProps.title}
                </Typography>
                <Typography className={props.classes.tableSubHeader}>
                  {toolbarProps.infoText}
                </Typography>
              </Grid>
              }
              <Grid item className={props.classes.toolbarTopRight}>
                {toolbarProps.renderTopRight()}
              </Grid>
            </Grid>
          </Toolbar>
          <Table {...getTableProps()}>
            <TableHead>
              {headerGroups.map((header_group) => renderHeader(props, header_group))}
            </TableHead>
            <TableBody>
              {rows.map(
                (row, i) =>
                  prepareRow(row) || (
                    <TableRow {...{
                      ...row.getRowProps(),
                      className: props.classes['row'],
                      onClick: (event) => props.onRowClick(event, row),
                    }}>
                      {row.cells.map(cell => {
                        return (
                          <TableCell {...cell.getCellProps()}>
                            <Typography className={props.classes.tableRow}>
                              {cell.render('Cell')}
                            </Typography>
                          </TableCell>
                        );
                      })}
                    </TableRow>
                  )
              )}
            </TableBody>
          </Table>
          <TablePagination {...{
            rowsPerPage,
            onChangeRowsPerPage: props.onChangeRowsPerPage,
            rowsPerPageOptions: [10, 25, 50],
            labelRowsPerPage: 'Rows per page',
            page,
            onChangePage: props.onChangePage,
            component: 'div',
            count: Infinity,
            labelDisplayedRows: ({ from, to, count }) => `Results ${from}-${to}`,
            backIconButtonProps: {
              'aria-label': 'previous page',
            },
            nextIconButtonProps: {
              'aria-label': 'next page',
            },
          }}/>
        </Grid>
      </Grid>
    </Paper>
  );
};

function TablePane(props) {
  return (
    <div
      data-test-id='TablePane'
      className={props.classes.tablePaneDiv}
    >
      {renderTable(props)}
    </div>
  );
}

TablePane.propTypes = {
  toolbarProps: PropTypes.object,
  reactTableProps: PropTypes.object,
  currentObject: PropTypes.object,
  onRowClick: PropTypes.func,
  enableFilters: PropTypes.bool,
  onFilterKeyDown: PropTypes.func,
  headerToFilterInput: PropTypes.object,
  rowsPerPage: PropTypes.number,
  onChangePage: PropTypes.func,
  onChangeRowsPerPage: PropTypes.func,
};

TablePane.defaultProps = {
  enableFilters: false,
  onFilterKeyDown: _.noop,
  onRowClick: _.noop,
  headerToFilterInput: {},
  rowsPerPage: 10,
};

export default withStyles(themeToStyles)(TablePane);
