import React from 'react';
import { withTranslation } from 'react-i18next';
import { compose } from 'redux';
import PropTypes from 'prop-types';
import _ from 'lodash';
import {
  Dialog,
  DialogContent,
  DialogActions,
  Button,
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import themeToStyles from './styles.js';

const ConfirmationDialog = (props) => {
  return (
    <div
      data-test-id='ConfirmationDialog'
    >
      <Dialog {...{
        // our default dialog props
        onClose: props.onClose,
        fullWidth: false,
        maxWidth: 'md',
        'aria-labelledby': 'default-dialog-title',
        'aria-describedby': 'default-dialog-description',
        BackdropProps: {
          classes: {
            root: props.classes.root,
          },
        },
        PaperProps: {
          classes: {
            root: props.classes.paper,
          },
        },
        // props you pass in override our defaults
        ...props.dialogProps,
      }}>
        <DialogContent
          classes={{
            root: props.classes.dialogContentRoot,
          }}
        >
          {props.message}
        </DialogContent>
        <DialogActions>
          <Button {...{
            className: props.classes.okButton,
            onClick: () => {props.onClickOk(); props.onClose();},
          }}>
            {props.t('yes')}
          </Button>
          <Button {...{
            className: props.classes.cancelButton,
            onClick: () => {props.onClickCancel(); props.onClose();},
          }}>
            {props.t('no')}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

ConfirmationDialog.propTypes = {
  message: PropTypes.string,
  dialogProps: PropTypes.object,
  onClickOk: PropTypes.func,
  onClickCancel: PropTypes.func,
  onClose: PropTypes.func,
};

ConfirmationDialog.defaultProps = {
  onClickOk: _.noop,
  onClickCancel: _.noop,
  onClose: _.noop,
};

export default compose(
  withTranslation(),
  withStyles(themeToStyles),
)(ConfirmationDialog);
