import {GENERIC} from 'src/store/actionTypes.js';

const initialState = {
  // This initial value is just a really good guess (to prevent a lag on load), but will be overwritten by what's in commons.json
  managedObjectTypes: ['user', 'role', 'assignment'],
  notifications: [],
};

export default (state=initialState, action) => {
  switch(action.type) {
  case GENERIC.CHANGE_MANAGED_OBJECT_TYPES:
    return {
      ...state,
      managedObjectTypes: action.payload,
    };
  case GENERIC.ENQUEUE_SNACKBAR:
    return {
      ...state,
      notifications: [
        ...state.notifications,
        {
          key: action.key,
          ...action.notification,
        },
      ],
    };
  case GENERIC.CLOSE_SNACKBAR:
    return {
      ...state,
      notifications: state.notifications.map(notification => (
        (action.dismissAll || notification.key === action.key)
          ? { ...notification, dismissed: true }
          : { ...notification }
      )),
    };
  case GENERIC.REMOVE_SNACKBAR:
    return {
      ...state,
      notifications: state.notifications.filter(
        notification => notification.key !== action.key,
      ),
    };
  default:
    return state;
  }
};
