import __ from '@hubcitymedia/commons-scripts/src/utils/jsUtils.js';
import CONSTANT from '@hubcitymedia/commons-scripts/src/utils/globalConstants.js';

/**
 * A glossary key sorter for the frontend only
 *
 * @param      {iterable}  keys_iterable  An iterable of glossary keys.
 * @return     {Array}   The keys in order.
 */
export function sortKeys(keys_iterable) {
  return __.sortedByPrecedence(keys_iterable, CONSTANT.GLOSSARY_KEY_PRIORITIZATION_ORDER, CONSTANT.GLOSSARY_LAST_KEY_ORDER);
}
