import _ from 'lodash';
import qf from '@hubcitymedia/commons-scripts/src/utils/queryFilterBuilder.js';
import glossaryUtils from '@hubcitymedia/commons-scripts/src/utils/glossaryUtils.js';

import {GLOSSARY} from 'src/store/actionTypes.js';
import {httpFailure, alert} from 'src/store/actions/generic.actions.js';
import http from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';

// note(ML): We may need to distinguish between a new obj vs a changed object: make separate onSaveNew and onUpdateExisting
export const onSaveObjectHandler = (updated_object) => (dispatch) => {
  // send request to server to save it
  const url = constants.IDM_CONTEXT + '/commons/glossary/put';
  const body = updated_object;
  return http.post(url, body)
    .then(() => {
      dispatch(setObjectEditorOpen(false));
      dispatch(alert('Glossary object saved to database', 'success'));
      // update the current object
      dispatch(currentObjectChangeHandler(updated_object));
      // update the save status
      dispatch(objectSavedStatusChangeHandler(true));
      // refresh the search results
      dispatch(updateSearchResultsHandler());
    }).catch(error => {
      dispatch(httpFailure(error, 'error'));
    });
};

export const objectSavedStatusChangeHandler = (new_status) => {
  return {
    type: GLOSSARY.IS_OBJECT_EDITS_SAVED_CHANGE,
    payload: {
      isObjectEditsSaved: new_status,
    },
  };
};

export const onDeleteObjectHandler = (obj_id) => (dispatch, getState) => {
  // send request to server to save it
  const url = `${constants.IDM_CONTEXT}/commons/glossary/${obj_id}`;
  return http.delete(url)
    .then(() => {
      dispatch(alert('Glossary object deleted from database', 'success'));
      // update the current object
      const current_class = getState().glossary.objectSearchClass;
      const new_obj = glossaryUtils.getNewObjectTemplate(current_class);
      dispatch(currentObjectChangeHandler(new_obj));
      // refresh the search results
      dispatch(updateSearchResultsHandler());
      dispatch(setObjectEditorOpen(false));
    }).catch(error => {
      dispatch(httpFailure(error, 'error'));
    });
};

export const currentObjectChangeHandler = (new_object) => {
  // current obj change
  return {
    type: GLOSSARY.CURRENT_OBJECT_CHANGE,
    payload: {
      currentObject: new_object,
    },
  };
};

export const objectsQuerySuccess = (response) => {
  const glossary_objects = response.data.result;
  return {
    type: GLOSSARY.FOUND_OBJECTS_CHANGE,
    payload: {
      foundObjects: glossary_objects,
    },
  };
};

/**
 * Calculates the query filter.
 *
 * @param      {string}  object_class  The object class ('object', 'system', 'system-attribute', etc).
 * @param      {object}  key_to_input  Dictionary of key -> user-input for filtering.
 * @return     {string}  The query filter.
 */
function computeQueryFilter(object_class, key_to_input) {
  const input_queries = _.map(key_to_input, (input, key) => qf.co(key, input));
  const input_query = qf.and(input_queries);
  const query_filter = qf.and([qf.eq('class', object_class), input_query]);
  return query_filter;
}

export const modifyFilter = (key, new_value) => (dispatch, getState) => {
  const filter_key_to_input = _.clone(getState().glossary.filterKeyToInput);
  filter_key_to_input[key] = new_value;
  dispatch(filterKeyToInputChangeHandler(filter_key_to_input));

};

export const filterKeyToInputChangeHandler = (new_dict) => (dispatch, getState) => {
  // update the dictionary
  dispatch({
    type: GLOSSARY.FILTER_KEY_TO_INPUT_CHANGE,
    payload: {
      filterKeyToInput: new_dict,
    },
  });
  // update the query filter
  const obj_class = getState().glossary.objectSearchClass;
  const query_filter = computeQueryFilter(obj_class, new_dict);
  dispatch(searchCriteriaChangeHandler(query_filter));
};

export const queryFilterChangeHandler = (query_filter) => {
  return {
    type: GLOSSARY.QUERY_FILTER_CHANGE,
    payload: {
      queryFilter: query_filter,
    },
  };
};

export const searchCriteriaChangeHandler = (query_filter) => (dispatch) => {
  // update qf in state
  dispatch(queryFilterChangeHandler(query_filter));
  // update search results
  dispatch(updateSearchResultsHandler());
};

export const objectSearchClassChangeHandler = (new_class) => (dispatch, getState) => {
  // update the object class
  dispatch({
    type: GLOSSARY.CLASS_CHANGE,
    payload: {
      objectSearchClass: new_class,
    },
  });
  // clear out the column filters
  const filter_key_to_input = {};
  dispatch({
    type: GLOSSARY.FILTER_KEY_TO_INPUT_CHANGE,
    payload: {
      filterKeyToInput: filter_key_to_input,
    },
  });
  // update the query filter
  const query_filter = computeQueryFilter(new_class, filter_key_to_input);
  dispatch(searchCriteriaChangeHandler(query_filter));
};

export const onChangePageSize = (new_page_size) => (dispatch, getState) => {
  // update the page size
  dispatch({
    type: GLOSSARY.PAGE_SIZE_CHANGE,
    payload: {
      pageSize: new_page_size,
    },
  });
  // update the offset
  // when you change the page size, you also change the offset. this is because the offset should always be a multiple of the page size.
  const old_offset = getState().glossary.pagedResultsOffset;
  const new_offset = Math.floor(old_offset / new_page_size) * new_page_size;
  dispatch({
    type: GLOSSARY.PAGED_RESULTS_OFFSET_CHANGE,
    payload: {
      pagedResultsOffset: new_offset,
    },
  });
  // refresh the results
  dispatch(updateSearchResultsHandler());
};

export const onChangePagedResultsOffset = (new_offset) => (dispatch) => {
  // update the offset
  dispatch({
    type: GLOSSARY.PAGED_RESULTS_OFFSET_CHANGE,
    payload: {
      pagedResultsOffset: new_offset,
    },
  });
  // refresh the results
  dispatch(updateSearchResultsHandler());
};

export const updateSearchResultsHandler = () => (dispatch, getState) => {
  // take query filter from state
  const query_filter = getState().glossary.queryFilter;
  const config = {
    params: {
      queryFilter: query_filter,
      pageSize: getState().glossary.pageSize,
      pagedResultsOffset: getState().glossary.pagedResultsOffset,
      sortKeys: 'displayName',
    },
  };
  return http.get(constants.IDM_CONTEXT + '/commons/glossary', config)
    .then((response) => {
      dispatch(objectsQuerySuccess(response));
    }).catch((error) => {
      dispatch(httpFailure(error, 'warning'));
    });
};

export const setObjectEditorOpen = isOpen => {
  return {
    type: GLOSSARY.SET_OBJECT_EDITOR_OPEN,
    payload: isOpen,
  };
};