import React from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import {connect} from 'react-redux';
import {withTranslation} from 'react-i18next';
import 'core-js/features/set';
import _ from 'lodash';
import is from 'check-types';
import { withStyles } from '@material-ui/core/styles';

import TablePane from 'src/components/TablePane/TablePane.js';
import { Button, Grid } from '@material-ui/core';
import {
  currentObjectChangeHandler, 
  updateSearchResultsHandler, 
  modifyFilter, 
  objectSavedStatusChangeHandler, 
  onChangePageSize, 
  onChangePagedResultsOffset,
  setObjectEditorOpen,
} from 'src/store/actions/glossary.actions.js';

import themeToStyles from './styles.js';

const CLASS_TO_TABLE_KEYS = {
  object: ['displayName'],
  identity: ['attributeName'],
  'identity-value': ['attributeName', 'attributeValue'],
  system: ['name'],
  'system-attribute': ['system', 'attributeName'],
  'system-value': ['system', 'attributeName', 'attributeValue'],
  'requestable-item-bundle': ['name'],
};

function keyToHeader(key) {
  // For now, the headers will be exactly the keys.
  // In the future, we can make them prettier, at the risk of being ambiguous/confusing.
  return key;
}

function keyToColumn(key) {
  const header = keyToHeader(key);
  return {
    Header: header,
    accessor: key,
  };
}

function getColumns(glossary_object_class) {
  // We need a column for each object key, with the exception of object keys where the value is blank and keys we never want to show.
  const keys_sorted = CLASS_TO_TABLE_KEYS[glossary_object_class];
  const columns = _.map(keys_sorted, keyToColumn);
  return columns;
}

class GlossarySearchResults extends React.Component {
  refreshSearch = () => {
    this.props.onSearchRefresh();
  }
  onRowClick = (row) => {
    // if they clicked on a non-blank row
    if (!is.undefined(row)) {
      const glossary_object = row.original;
      this.props.onObjectSelect(glossary_object);
      // update save status to say its saved
      this.props.setObjectEditorOpen(true);
      this.props.changeSavedStatus(true);
    }
  }
  onFilterKeyDown = (event, column_name) => {
    // if they hit 'Enter', apply the search filter
    // if (event.key === 'Enter') {
    this.props.modifyFilter(column_name, event.target.value);
    // }
  }
  onChangePageSize = (event) => {
    const new_page_size = event.target.value;
    this.props.onChangePageSize(new_page_size);
  }
  onChangePage = (event, new_page_num) => {
    // convert page number to results offset
    const new_results_offset = new_page_num * this.props.pageSize;
    this.props.onChangePagedResultsOffset(new_results_offset);
  }
  getPage = () => {
    // convert results offset to page number
    const results_offset = this.props.pagedResultsOffset;
    const page_num = results_offset / this.props.pageSize;
    return page_num;
  }
  renderRefreshButton = () => {
    return (
      <Button {...{
        onClick: this.refreshSearch,
        className: this.props.classes['button'],
        variant: 'outlined',
        color: 'primary',
        size: 'medium',
      }}>
        {this.props.t('refresh results')}
      </Button>
    );
  }
  render = () => {
    let {foundObjects} = this.props;
    return (
      <Grid container
        data-test-id='GlossarySearchResults'
      >
        <Grid item>
          <TablePane {...{
            toolbarProps: {
              title: this.props.objectClass,
              renderTopRight: this.props.renderNewButton,
              infoText: this.props.t(`glossary-class-info.${this.props.objectClass}`),
            },
            reactTableProps: {
              data: foundObjects,
              columns: getColumns(this.props.objectClass),
            },
            currentObject: this.props.currentObject,
            onRowClick: (event, row) => this.onRowClick(row),
            enableFilters: true,
            onFilterKeyDown: this.onFilterKeyDown,
            headerToFilterInput: this.props.filterKeyToInput,
            rowsPerPage: this.props.pageSize,
            onChangeRowsPerPage: this.onChangePageSize,
            page: this.getPage(),
            onChangePage: this.onChangePage,
          }}/>
        </Grid>
      </Grid>
    );
  }
}

GlossarySearchResults.propTypes = {
  foundObjects: PropTypes.array.isRequired,
  currentObject: PropTypes.object,
  onObjectSelect: PropTypes.func,
  onSearchRefresh: PropTypes.func,
  t: PropTypes.func,
};

const mapStateToProps = (state) => {
  return {
    foundObjects: state.glossary.foundObjects,
    objectClass: state.glossary.objectSearchClass,
    currentObject: state.glossary.currentObject,
    filterKeyToInput: state.glossary.filterKeyToInput,
    pageSize: state.glossary.pageSize,
    pagedResultsOffset: state.glossary.pagedResultsOffset,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onObjectSelect: (object) => dispatch(currentObjectChangeHandler(object)),
    onSearchRefresh: () => dispatch(updateSearchResultsHandler()),
    modifyFilter: (col_name, new_value) => dispatch(modifyFilter(col_name, new_value)),
    changeSavedStatus: (new_status) => dispatch(objectSavedStatusChangeHandler(new_status)),
    onChangePageSize: (new_page_size) => dispatch(onChangePageSize(new_page_size)),
    onChangePagedResultsOffset: (new_page_offset) => dispatch(onChangePagedResultsOffset(new_page_offset)),
    setObjectEditorOpen: (isOpen) => dispatch(setObjectEditorOpen(isOpen)),
  };
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(themeToStyles)
)(GlossarySearchResults);
