import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import {withTranslation} from 'react-i18next';
import _ from 'lodash';
import is from 'check-types';
import { compose } from 'redux';
import {withFormik} from 'formik';
import { withStyles } from '@material-ui/core/styles';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import clsx from 'clsx';
import __ from '@hubcitymedia/commons-scripts/src/utils/jsUtils';
import CONSTANT from '@hubcitymedia/commons-scripts/src/utils/globalConstants';
import commonUtils from '@hubcitymedia/commons-scripts/src/utils/commonUtils';

import ManagedObjectPicker from 'src/components/ManagedObjectPicker';
import { 
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid, 
  Input,
  InputAdornment,
  IconButton,
  InputLabel,
  FormControl, 
  FormLabel, 
  FormHelperText, 
  Select, 
  Typography,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Option from 'src/components/Option';

import themeToStyles from './styles.js';

const TYPE_OPTIONS = CONSTANT.TYPES;
const DEFAULT_TYPE = 'string';
// internal to here
// note(ML): IDM allows any weird characters in keys of objects, so in rare cases this could cause conflicts
const TYPE_SUFFIX = '-->type';
function typeToDefaultValue(_type) {
  switch (_type) {
  case 'string':
    return '';
  case 'boolean':
    return false;
  case 'integer':
    return 0;
  case 'id':
    return '';
  case 'date string':
    return __.userInputtedValueToDateString(new Date());
  case 'managed object id':
    return '';
  case 'object':
    return {};
  case 'array':
    return [];
  default:
    __.requestError('default value for this type not implemented');
  }
}

function getType(thing) {
  const type = is.object(thing) ?
    'object' :
    is.array(thing) ?
      'array' :
      is.integer(thing) ?
        'int' :
        is.boolean(thing) ?
          'bool' :
          is.string(thing) ?
            'string' :
            _.isUndefined(thing) ?
              'undefined' :
              __.requestError(`detection for the type ${typeof thing} not implemented`);
  return type;
}

function renderOption(option) {
  return (
    <Option
      key={option}
      value={option}
    >
      {option}
    </Option>
  );
}

function renderOptions(options) {
  return options.map(renderOption);
}

function renderBooleanOptions() {
  const boolOptions = [true, false];
  return boolOptions.map(opt => (
    <Option
      key={JSON.stringify(opt)}
      value={opt}
    >
      {JSON.stringify(opt)}
    </Option>
  ));
}

function valueToString(value) {
  // Given a value to display in an input box, return the appropriate string.
  if (is.null(value) || is.undefined(value)) {
    return '';
  } else if (is.string(value)) {
    return value;
  } else if (is.boolean(value)) {
    return value;
  } else if (is.object(value)) {
    return JSON.stringify(value);
  } else {
    return value.toString();
  }
}

class ObjectEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isNewDropdownOpen: false,
      isModalOpenDict: {},
      // The user input of the box that allows the user to create a new key.  To the left of the "+" button.
      createKeyInput: '',
    };
  }
  handleCreateKeyInputChange = (event) => {
    this.setState({
      createKeyInput: event.target.value,
    });
  }
  toggleNewDropdown = () => {
    this.setState({
      isNewDropdownOpen: !this.state.isNewDropdownOpen,
    });
  }
  toggleModal = (key) => {
    const is_open_dict = _.cloneDeep(this.state.isModalOpenDict);
    // a missing key is the same as false
    if (!is_open_dict.hasOwnProperty(key)) {
      is_open_dict[key] = false;
    }
    // toggle the boolean
    is_open_dict[key] = !is_open_dict[key];
    this.setState({
      isModalOpenDict: is_open_dict,
    });
  }
  getObjectCopy = () => {
    const values_copy = _.cloneDeep(this.props.values);
    // weed out the types
    let object_copy = _.pickBy(values_copy, (value, key) => !_.endsWith(key, TYPE_SUFFIX));
    if (this.props.objectType === 'array') {
      object_copy = _.toArray(object_copy);
    }
    return object_copy;
  }
  getConstraintsObjectCopy = () => {
    if (!this.props.enableConstraints) {
      return {};
    }
    const values_copy = _.cloneDeep(this.props.values);
    // only include the type keys
    const type_object_suffixed_copy = _.pickBy(values_copy, (value, key) => _.endsWith(key, TYPE_SUFFIX));
    // remove the suffix from the keys
    const regex = new RegExp(TYPE_SUFFIX + '$');
    const type_object_copy = _.mapKeys(type_object_suffixed_copy, (value, key) => key.replace(regex, ''));
    // build the constraints object from the types
    let constraints_object_copy = _.mapValues(type_object_copy, (value) => {return {type: value};});
    if (this.props.objectType === 'array') {
      constraints_object_copy = _.toArray(constraints_object_copy);
    }
    return constraints_object_copy;
  }
  getObjectType = () => {
    return this.props.objectType;
  }
  saveObject = () => {
    let form_object = this.getObjectCopy();
    let form_constraints_object = this.getConstraintsObjectCopy();
    // execute callback
    this.props.onSave(form_object, form_constraints_object);
  }
  deleteObject = () => {
    const form_object = this.getObjectCopy();
    this.props.onDelete(form_object);
    
  }
  deleteKey = (key) => {
    // change the value to undefined
    const form_object = this.getObjectCopy();
    const form_constraints_object = this.getConstraintsObjectCopy();
    const type = this.getObjectType();
    // remove the key
    if (type === 'array') {
      form_object.splice(key, 1);
    }
    else {
      delete form_object[key];
    }
    // remove the constraints for the key
    if (type === 'array' && _.isArray(form_constraints_object)) {
      form_constraints_object.splice(key, 1);
    }
    else {
      delete form_constraints_object[key];
    }
    // update
    this.props.onDeleteKey(form_object, form_constraints_object);
    this.props.onChange();
  }
  createKey = () => {
    const form_object = this.getObjectCopy();
    const form_constraints_object = this.getConstraintsObjectCopy();
    const type = this.getObjectType();
    // create the new key
    const new_value = '';
    if (type === 'array') {
      form_object.push(new_value);
    }
    else {
      const new_key = this.state.createKeyInput;
      // a blank string will not be allowed as a key
      if (new_key === '') {
        return;
      }
      form_object[new_key] = new_value;
    }
    const new_type = DEFAULT_TYPE;
    const new_key = this.state.createKeyInput;
    form_constraints_object[new_key] = {};
    form_constraints_object[new_key]['type'] = new_type;
    // update obj in form
    this.props.onCreateKey(form_object, form_constraints_object);
    // clear value from input field
    if (type === 'array') {
      // pass
    }
    else {
      this.setState({
        createKeyInput: '',
      });
    }
    // ping a change
    this.props.onChange();
  }
  handleKeyDownForCreateKey = (event) => {
    // if they hit 'Enter', create the key
    if (event.key === 'Enter') {
      this.createKey();
    }
  }
  keyToKeyValue = (key, object, constraints_object) => {
    if (_.isUndefined(object)) {
      throw 'you forgot to pass in the object';
    }
    if (_.isUndefined(constraints_object)) {
      throw 'you forgot to pass in the constraints object';
    }
    const value1 = object[key];
    const type1 = getType(value1);
    const type2 = commonUtils.getTypeFromConstraintsObject(key, constraints_object);
    const value = value1;
    const type = type2 || type1;
    const is_disabled = this.props.isDisabled(key, object);
    const is_type_disabled = this.props.isTypeDisabled(key, object);
    const is_dropdown = this.props.isDropdown(key, object) || type === 'boolean';
    const is_managed_object = this.props.isManagedObject(key, object, type);
    const values = this.props.getValuesForKey(key, object);
    return {
      key,
      value,
      type,
      values,
      isDropdown: is_dropdown,
      isManagedObject: is_managed_object,
      isDisabled: is_disabled,
      isTypeDisabled: is_type_disabled,
    };
  }
  renderCancelButton = () => {    
    return (
      <Button {...{
        className: this.props.classes.button,
        onClick: this.props.handleClose,
        variant: 'outlined',
        color: 'primary',
        size: 'medium',
      }}>
        {this.props.t('cancel')}
      </Button>
    );
  }
  renderDeleteKeyButton = (key, object) => {
    return (
      <Button {...{
        className: this.props.classes['delete-button'],
        disabled: !this.props.isKeyDeletable(key, object),
        onClick: this.props.isKeyDeletable(key, object) ? this.deleteKey.bind(this, key) : _.noop,
        color: 'secondary',
      }}>
        <DeleteIcon />
      </Button>
    );
  }
  renderChild = (key_value) => {
    const onUpdateLocally = (child_object, child_constraints_object) => {
      const parent_object = this.getObjectCopy();
      parent_object[key_value.key] = child_object;
      const parent_object_constraints = this.getConstraintsObjectCopy();
      this.props.onUpdateLocally(parent_object, parent_object_constraints);
    };
    const title = key_value.key;
    return (
      <Dialog 
        className={this.props.classes.objectEditorDialog}
        open={this.state.isModalOpenDict[key_value.key] || false} 
        onClose={this.toggleModal.bind(this, key_value.key)} 
      >
        <ObjectEditorExtended {...{
          object: this.getObjectCopy()[key_value.key],
          objectType: key_value.type,
          enableConstraints: false,
          title: title,
          handleClose: this.toggleModal.bind(this, key_value.key),
          showNewButton: false,
          // on save, update the current object to be what the form object is
          onSave: (child_object) => {
            onUpdateLocally(child_object);
            this.toggleModal(key_value.key);
          },
          onDelete: (child_object) => {
            // replace the object with a blank value
            const parent_object = this.getObjectCopy();
            parent_object[key_value.key] = '';
            const parent_object_constraints = this.getConstraintsObjectCopy();
            this.props.onUpdateLocally(parent_object, parent_object_constraints);
            // note that even without this line, the modal would disappear bc the component rerenders.  However, this keeps the isModalOpenDict accurate.
            this.toggleModal(key_value.key);
          },
          onUpdateLocally: onUpdateLocally,
          onChange: this.props.onChange,
          onCreateKey: onUpdateLocally,
          onDeleteKey: onUpdateLocally,
        }} />
      </Dialog>
    );
  }
  renderChildAccessor = (key_value) => {
    // this includes the (hidden) child itself
    const type = key_value.type;
    if (type === 'object') {
      return this.renderInput(key_value);
    }
    return (
      <Fragment>
        <Button {...{
          className: this.props.classes.button,
          onClick: this.toggleModal.bind(this, key_value.key),
          variant: 'outlined',
        }}>
          [ ... ]
        </Button>
        {this.renderChild(key_value)}
      </Fragment>
    );
  }
  getSharedInputProps = (key_value) => {
    // props shared by Input, Dropdown, and potentially other types of inputs
    return {
      disabled: key_value.isDisabled,
      name: key_value.key,
      key: 'value',
      onChange: (event) => {
        this.props.handleChange(event);
        this.props.onChange();
      },
      onBlur: this.props.handleBlur,
    };
  }
  onManagedObjectChange = (full_obj_id, key) => {
    // imperatively update the value
    this.props.setFieldValue(key, full_obj_id);
    this.props.onChange();
  }
  renderManagedObjectPicker = (key_value) => {
    return (
      <ManagedObjectPicker {...{
        initialManagedObjectId: key_value.value,
        // when the selected managed object changes
        onChange: (new_id) => this.onManagedObjectChange(new_id, key_value.key),
      }}/>
    );
  }
  renderDropdown = (key_value) => {
    const valueString = valueToString(key_value.value);
    const valuesStrings = key_value.values.map(valueToString);
    return (
      <Select {...{
        ...this.getSharedInputProps(key_value),
        labelId: key_value.key,
        value: valueString,
      }}>
        {key_value.type === 'boolean' ? renderBooleanOptions() : renderOptions(valuesStrings)}
      </Select>
    );
  }
  renderInput = (key_value) => {
    return (
      <Input {...{
        ...this.getSharedInputProps(key_value),
        id: key_value.key,
        type: 'text',
        value: valueToString(key_value.value),
      }}/>
    );
  }
  onTypeChange = (event, e2, key, type) => {
    const old_type = type;
    const new_type = event.target.value;
    if (old_type !== new_type) {
      // force-update the value
      this.props.values[key] = typeToDefaultValue(new_type);
      // let formik do its regular thing
      this.props.handleChange(event);
    }
    this.props.onChange();
  }
  renderType = (key_value) => {
    return (
      <Select {...{
        className: this.props.classes.glossaryInput,
        disabled: key_value.isTypeDisabled,
        name: key_value.key + TYPE_SUFFIX,
        key: 'type',
        onChange: (event, e2) => this.onTypeChange(event, e2, key_value.key, key_value.type),
        onBlur: this.props.handleBlur,
        value: key_value.type,
      }}>
        {
          TYPE_OPTIONS.map((option) => {
            return (
              <Option
                key={option}
                value={option}
              >
                {this.props.t(option)}
              </Option>
            );
          })
        }
      </Select>
    );
  }
  renderKeyValue = (key_value, obj) => {
    // compute val
    const rendered_value = ['object', 'array'].includes(key_value.type) ?
      this.renderChildAccessor(key_value) :
      key_value.isDropdown ?
        this.renderDropdown(key_value) :
        key_value.isManagedObject ?
          this.renderManagedObjectPicker(key_value) :
          this.renderInput(key_value) ;
    return (
      <Grid item key={[obj._id, key_value.key]} className={this.props.classes.glossaryInputGroup}>
        {this.renderDeleteKeyButton(key_value.key, obj)}
        <FormLabel className={this.props.classes.formLabel} htmlFor={key_value.key}>{key_value.key}</FormLabel>
        <div className={this.props.classes.input}>
          {this.props.enableConstraints && this.renderType(key_value)}
          {rendered_value}
        </div>
        <FormHelperText>{this.props.errors.class && this.props.touched.class && this.props.errors.class}</FormHelperText>
      </Grid>
    );
  }
  renderKeyValues = (object, constraints_object) => {
    // Given an object, render its key-value pairs in the editor.
    this.props.validateObject(object);
    const keys = this.props.getKeysForEditor(object);
    const sorted_keys = this.props.sortKeys(keys);
    const key_values = _.map(sorted_keys, (key) => this.keyToKeyValue(key, object, constraints_object));
    const rendered_key_values = key_values.map(key_value => {
      return this.renderKeyValue(key_value, object);
    });
    return rendered_key_values;
  }
  renderInvisibleButton = () => {
    // note(ML): only here for CSS reasons, to make the create-key input key and button space exactly the same as the edit-key inputs above it.
    return (
      <Button {...{
        className: clsx([
          this.props.classes['invisible'],
          this.props.classes.button,
        ]),
        disabled: true,
        variant: 'contained',
        color: 'secondary',
      }}>
        <DeleteIcon />
      </Button>
    );
  }
  renderCreateKeyButton = () => {
    return (
      <Button {...{
        className: clsx([
          this.props.classes['create-key-button'],
          this.props.classes.button,
        ]),
        onClick: this.createKey,
        variant: 'contained',
        color: 'secondary',
      }}>
        <AddIcon />
      </Button>
    );
  }
  renderCreateKeyInput = () => {
    return (
      <FormControl className={this.props.classes.newKeyFormControl}>
        <InputLabel htmlFor="add-new-key">{this.props.t('glossary-new-key')}</InputLabel>
        <Input
          id="add-new-key"
          type='text'
          value={this.state.createKeyInput}
          onKeyDown={this.handleKeyDownForCreateKey}
          onChange={this.handleCreateKeyInputChange}
          endAdornment={
            <InputAdornment className={this.props.classes.inputAdornment} position='end' variant='filled'>
              <IconButton
                aria-label={this.props.t('glossary-new-key-aria-label')}
                onClick={this.createKey}
              >
                {this.state.createKeyInput ? <AddIcon color='primary' /> : <AddIcon color='disabled' /> }
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>
    );
  }
  renderCreateKeyUI = () => {
    const type = this.getObjectType();
    return (type === 'object') ? this.renderCreateKeyInput() : this.renderCreateKeyButton();
  }
  renderReloadButton = () => {
    return (
      <Button {...{
        className: this.props.classes['button'],
        onClick: this.createObject,
        variant: 'outlined',
        color: 'primary',
        size: 'medium',
      }}>
        {this.props.t('reload')}
      </Button>
    );
  }
  renderSaveButton = () => {
    return (
      <Button {...{
        className: this.props.classes['button'],
        onClick: this.saveObject,
        variant: 'contained',
        color: 'primary',
        size: 'medium',
      }}>
        {this.props.saveButtonContent || this.props.t('save')}
      </Button>
    );
  }
  renderDeleteButton = () => {
    return (
      <Button {...{
        className: clsx(this.props.classes['button'], this.props.classes.deleteButton),
        onClick: this.deleteObject,
        variant: 'outlined',
        color: 'inherit',
        size: 'medium',
      }}>
        {this.props.deleteButtonContent || this.props.t('delete')}
      </Button>
    );
  }
  renderButtons = () => {
    return (
      <Grid item className={this.props.classes.buttonGrid}>
        {this.renderCancelButton()}
        {this.props.showDeleteButton && this.props.object._id ? this.renderDeleteButton() : null}
        {this.props.showSaveButton ? this.renderSaveButton() : null}
      </Grid>
    );
  }
  render = () => {
    return (
      <Fragment>
        { this.props.title && 
        <DialogTitle id={this.props.title} disableTypography>
          <Typography variant='h6'>{this.props.title}</Typography>
          <IconButton aria-label={this.props.t('label-close')} className={this.props.classes.closeButton} onClick={this.props.handleClose}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        }
        <DialogContent dividers={!this.props.title} className={this.props.classes.dialog}>
          <div className={this.props.classes.container}>
            {this.renderKeyValues(this.getObjectCopy(), this.getConstraintsObjectCopy())}
            {this.props.showCreateKeyUI ? this.renderCreateKeyUI() : null}
          </div>
        </DialogContent>
        <DialogActions>
          {this.renderButtons()}
        </DialogActions>
      </Fragment>
    );
  }
}

ObjectEditor.propTypes = {
  deleteButtonContent: PropTypes.element,
  getDescription: PropTypes.func,
  getKeysForEditor: PropTypes.func,
  getValidationMessage: PropTypes.func,
  getValuesForKey: PropTypes.func,
  isKeyDeletable: PropTypes.func,
  isDisabled: PropTypes.func,
  isTypeDisabled: PropTypes.func,
  isDropdown: PropTypes.func,
  isManagedObject: PropTypes.func,
  showNewButton: PropTypes.bool,
  showSaveButton: PropTypes.bool,
  showDeleteButton: PropTypes.bool,
  showCreateKeyUI: PropTypes.bool,
  onNew: PropTypes.func,
  onSave: PropTypes.func,
  onChange: PropTypes.func,
  onUpdateLocally: PropTypes.func,
  onDelete: PropTypes.func,
  onCreateKey: PropTypes.func,
  onDeleteKey: PropTypes.func,
  modifiedFormObject: PropTypes.func,
  newButtonContent: PropTypes.element,
  saveButtonContent: PropTypes.element,
  sortKeys: PropTypes.func,
  title: PropTypes.string,
  validateObject: PropTypes.func,
  object: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.array,
  ]).isRequired,
  objectType: PropTypes.string.isRequired,
  constraintsObject: PropTypes.object,
  enableConstraints: PropTypes.bool,
};

ObjectEditor.defaultProps = {
  getKeysForEditor: _.keys,
  getDescription: () => '',
  getValidationMessage: () => '',
  getValuesForKey: () => [],
  isKeyDeletable: () => true,
  isDisabled: () => false,
  isTypeDisabled: () => false,
  isDropdown: () => false,
  isManagedObject: (key, object, type) => {return Boolean(type === 'managed object id');},
  modifiedFormObject: _.identity,
  sortKeys: __.sorted,
  showNewButton: true,
  showSaveButton: true,
  showDeleteButton: true,
  showCreateKeyUI: true,
  onChange: _.noop,
  validateObject: _.noop,
  constraintsObject: {},
  enableConstraints: true,
};

const formikConfig = {
  enableReinitialize: true,
  mapPropsToValues: (props) => {
    const values_obj = {};
    // object keys stay the same
    const object = props.object;
    _.assign(values_obj, object);
    // types from constraintsObject get suffixed keys
    const constraints_object = props.constraintsObject;
    const type_object = _.mapValues(constraints_object, (value) => value.type);
    const type_object_suffixed = _.mapKeys(type_object, (value, key) => key + TYPE_SUFFIX);
    _.assign(values_obj, type_object_suffixed);
    return values_obj;
  },
};

const ObjectEditorExtended = compose(
  withTranslation(),
  withStyles(themeToStyles),
  withFormik(formikConfig)
)(ObjectEditor);

export default ObjectEditorExtended;
