import { createMuiTheme } from '@material-ui/core/styles';

const muiBaseTheme = createMuiTheme();

const drawerWidth = muiBaseTheme.spacing() * 30;

const renameProp = (
  oldProp,
  newProp,
  { [oldProp]: old, ...others }
) => {
  return {
    [newProp]: old,
  };
};

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#314054',
    },
    secondary: {
      main: '#007bff',
    },
    forgerock: {
      blue: '#109cf1',
      darkGray: '#324054',
      lightGray: '#f6f8fa',
      white: '#fff',
    },
    background: {
      default: '#f6f8fa',
    },
  },
  overrides: {
    MuiButtonBase: {
      root: {
        height: '40px',
        '&.hide': {
          display: 'none',
        },
      },
    },
    MuiButton: {
      root: {
        fontWeight: '400',
        textTransform: 'capitalize',
      },
    },
    MuiTypography: {
      root: {
        'text-transform': 'capitalize',
      },
    },
    MuiToolbar: {
      root: {
        '&.appToolbar': {
          justifyContent: 'center',
          flexGrow: 1,
          transition: muiBaseTheme.transitions.create(['margin', 'width'], {
            easing: muiBaseTheme.transitions.easing.sharp,
            duration: muiBaseTheme.transitions.duration.leavingScreen,
          }),
        },
        '&.appToolbarLoggedIn': {
          justifyContent: 'space-between',
        },
        '&.menuButton': {
          marginRight: muiBaseTheme.spacing(2),
        },
        '&.hide': {
          display: 'none',
        },
      },
    },
    MuiDialog: {
      paper: {
        overflowY: 'visible',
      },
    },
    MuiGrid: {
      item: {
        flexGrow: 1,
        '&.mainContent': {
          ...renameProp('minHeight', 'marginTop', muiBaseTheme.mixins.toolbar),
          padding: muiBaseTheme.spacing(3),
          transition: muiBaseTheme.transitions.create('margin', {
            easing: muiBaseTheme.transitions.easing.sharp,
            duration: muiBaseTheme.transitions.duration.leavingScreen,
          }),
          marginLeft: 0,
        },
        '&.mainContent.contentShift': {
          transition: muiBaseTheme.transitions.create('margin', {
            easing: muiBaseTheme.transitions.easing.easeOut,
            duration: muiBaseTheme.transitions.duration.enteringScreen,
          }),
          marginLeft: drawerWidth,
        },
      },
    },
  },
});

export default theme;
