import React from 'react';
import PropTypes from 'prop-types';
import {compose} from 'redux';
import {connect} from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import _ from 'lodash';

import { Select } from '@material-ui/core';
import CONSTANT from '@hubcitymedia/commons-scripts/src/utils/globalConstants.js';
import idmUtils from '@hubcitymedia/commons-scripts/src/utils/idmUtils.js';
import {updateManagedObjectTypes, httpFailure} from 'src/store/actions/generic.actions.js';
import Option from 'src/components/Option';
import MultiSelectField from 'src/components/MultiSelectField';
import http from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';

import themeToStyles from './styles.js';

/**
 * Given a managed object, return something with a 'value' and 'label' key for display.
 */
const objectToSelectValue = (obj) => {
  return {
    value: obj._id,
    label: obj.displayName,
  };
};

const selectValueToObject = ({value, label}) => {
  return {
    _id: value,
    displayName: label,
  };
};

class ManagedObjectPicker extends React.Component {
  _isMounted = false;

  constructor(props) {
    super(props);
    // This can be calculated immediately, but not the REST call, so that goes in componentWillMount
    const managed_object_type = props.initialManagedObjectId ?
      idmUtils.getManagedObjectTypeFromLongId(props.initialManagedObjectId) :
      CONSTANT.DEFAULT_MANAGED_OBJECT_TYPE;
    this.state = {
      initialManagedObjectId: props.initialManagedObjectId,
      managedObjectType: managed_object_type,
      objectSelections: [],
      objectSuggestions: [],
    };
  }
  componentDidMount() {
    this._isMounted = true;
    // if there was an initial managed object and the selections are empty, then auto-populate it into selection box
    if (this.state.initialManagedObjectId && this.state.objectSelections.length === 0) {
      this.populateInitialSelection(this.state.initialManagedObjectId);
    }
    // update the managed object type list
    this.props.updateManagedObjectTypes();
  }
  componentWillUnmount() {
    this._isMounted = false;
  }
  populateInitialSelection = (long_managed_object_id) => {
    http.request({
      method: 'get',
      url: constants.IDM_CONTEXT + '/commons/' + long_managed_object_id,
    }).then((response) => {
      const managed_object = response.data.result;
      if (this._isMounted) {
        this.setState({objectSelections: [managed_object]});
      }
      return managed_object;
    }).catch(error => {
      return this.props.httpFailure(error, 'error');
    });
  }
  onChangeInput = (input_string) => {
    // get new suggestions
    const type = this.state.managedObjectType;
    http.request({
      method: 'get',
      url: constants.IDM_CONTEXT + '/commons/managed/' + type,
      params: {
        queryString: input_string,
        pageSize: 10,
      },
    }).then((response) => {
      const managed_object_suggestions = response.data.result;
      this.setState({objectSuggestions: managed_object_suggestions});
    }).catch(error => {
      return this.props.httpFailure(error, 'error');
    });
  }
  onChangeSelection = (new_selection) => {
    // When the user selects a managed object
    const new_selections = [new_selection];
    const new_object_selections = new_selections.map(selectValueToObject);
    this.setState({objectSelections: new_object_selections});
    // pass the change to the parent
    const new_object_selection = new_object_selections[0];
    const object_id = new_object_selection._id;
    const full_managed_object_id = `managed/${this.state.managedObjectType}/${object_id}`;
    this.props.onChange(full_managed_object_id);
  }
  onChangeType = (new_type) => {
    // if the new type is different than the old type
    if (new_type !== this.state.managedObjectType) {
      // clear the dropdown
      this.setState({objectSuggestions: []});
      // clear the typeahead
      this.setState({objectSelections: []});
      // update the type in state
      this.setState({managedObjectType: new_type});
    }
  }
  render = () => {
    return (
      <div
        data-test-id='ManagedObjectPicker'
        className={this.props.classes.container}
      >  
        {/* dropdown */}
        <Select {...{
          className: this.props.classes.managedObjectTypeSelect,
          name: 'select-managed-object-type',
          onChange: (event) => this.onChangeType(event.target.value),
          value: this.state.managedObjectType || '',
        }}>
          {
            this.props.managedObjectTypes.map((option) => {
              return (
                <Option
                  key={option}
                  value={option}
                >
                  {option}
                </Option>
              );
            })
          }
        </Select>
        {/* typeahead */}
        <MultiSelectField {...{
          type: 'typeahead',
          isDisabled: !this.state.managedObjectType,
          options: this.state.objectSuggestions.map(objectToSelectValue),
          // value of what they've selected (array of objects)
          value: this.state.objectSelections.map(objectToSelectValue),
          isMulti: false,
          onChange: this.onChangeSelection,
          // when you type
          onInputChange: _.debounce(this.onChangeInput, CONSTANT.DEBOUNCE_TYPEAHEAD_WAIT_TIME),
        }}/>
      </div>
    );
  }
}

ManagedObjectPicker.propTypes = {
  onChange: PropTypes.func,
  initialManagedObjectId: PropTypes.string,
};

ManagedObjectPicker.defaultProp = {
  onChange: _.noop,
  initialManagedObjectId: null,
};

const mapStateToProps = (state) => {
  return {
    // prop: state.path.to.value,
    // this gets fetched on load
    managedObjectTypes: state.generic.managedObjectTypes,
  };
};

const mapDispatchToProps = {
  updateManagedObjectTypes,
  httpFailure,
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(themeToStyles),
)(ManagedObjectPicker);
