import {AUTH} from 'src/store/actionTypes.js';

const initialState = { authModule: '', isLoggedIn: false, authenticationId: '', authorization: {}, error: false, message: '' };

export default (state = initialState, action) => {
  switch(action.type) {
  case AUTH.FETCH_AUTH_MODULE_SUCCESS:
    return {
      ...state,
      authModule: action.payload,
    };
  case AUTH.FETCH_AUT_MODULE_FAILURE:
    return {
      ...state,
      message: `${action.payload.code}: ${action.payload.message}`,
      error: true,
    };
  case AUTH.AUTHENTICATION_SUCCESS:
    return {
      ...state,
      authenticationId: action.payload.authenticationId,
      authorization: action.payload.authorization,
      isLoggedIn: true,
    };
  case AUTH.LOGOUT_SUCCESS:
    return initialState;
  default:
    return state;
  }
};
