let IDM_URL = process.env.IDM_URL;
let IDM_CONTEXT = process.env.IDM_CONTEXT;

if (process.env.NODE_ENV === 'production') {
  IDM_URL = `${location.protocol}//${location.hostname}`;
  if (location.port) {
    IDM_URL += `:${location.port}`;
  }
  IDM_CONTEXT = window.IDM_CONTEXT;
}

const constants = {
  IDM_URL,
  IDM_CONTEXT,
};

export default constants;
