import glossaryUtils from '@hubcitymedia/commons-scripts/src/utils/glossaryUtils.js';

import {GLOSSARY} from 'src/store/actionTypes.js';

const dummy_obj = {
  class: 'object',
  displayName: 'Dummy Object',
  objectId: '1',
  order: 1,
  constraints: {
    class: {
      type: 'string',
    },
    objectId: {
      type: 'managed object id',
    },
    requestable: {
      type: 'boolean',
    },
    order: {
      type: 'int',
    },
  },
};

const initialState = {
  filterKeyToInput: {},
  objectSearchClass: 'object',
  foundObjects: [dummy_obj],
  isObjectEditorOpen: false,
  isObjectEditsSaved: true,
  // for pagination
  pageSize: 10,
  pagedResultsOffset: 0,
};
initialState['currentObject'] = glossaryUtils.getNewObjectTemplate(initialState['objectSearchClass']);

export default (state=initialState, action) => {
  switch(action.type) {
  case GLOSSARY.SET_OBJECT_EDITOR_OPEN:
    return {
      ...state,
      isObjectEditorOpen: action.payload,
    };
  case GLOSSARY.CLASS_CHANGE:
    return {
      ...state,
      objectSearchClass: action.payload.objectSearchClass,
    };
  case GLOSSARY.FILTER_KEY_TO_INPUT_CHANGE:
    return {
      ...state,
      filterKeyToInput: action.payload.filterKeyToInput,
    };
  case GLOSSARY.QUERY_FILTER_CHANGE:
    return {
      ...state,
      queryFilter: action.payload.queryFilter,
    };
  case GLOSSARY.PAGE_SIZE_CHANGE:
    return {
      ...state,
      pageSize: action.payload.pageSize,
    };
  case GLOSSARY.PAGED_RESULTS_OFFSET_CHANGE:
    return {
      ...state,
      pagedResultsOffset: action.payload.pagedResultsOffset,
    };
  case GLOSSARY.FOUND_OBJECTS_CHANGE:
    return {
      ...state,
      foundObjects: action.payload.foundObjects,
    };
  case GLOSSARY.CURRENT_OBJECT_CHANGE:
    return {
      ...state,
      currentObject: action.payload.currentObject,
    };
  case GLOSSARY.IS_OBJECT_EDITS_SAVED_CHANGE:
    return {
      ...state,
      isObjectEditsSaved: action.payload.isObjectEditsSaved,
    };
  default:
    return state;
  }
};
