import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import {connect} from 'react-redux';
import {withTranslation} from 'react-i18next';
import { withStyles } from '@material-ui/core/styles';
import { Button, Grid } from '@material-ui/core';
import { Add } from '@material-ui/icons';

import glossaryUtils from '@hubcitymedia/commons-scripts/src/utils/glossaryUtils.js';
import GlossarySearchResults from 'src/components/GlossarySearchResults/GlossarySearchResults.js';
import GlossaryObjectEditor from 'src/components/GlossaryObjectEditor/GlossaryObjectEditor.js';
import PageTitle from 'src/components/PageTitle';
import { currentObjectChangeHandler, setObjectEditorOpen } from 'src/store/actions/glossary.actions.js';

import themeToStyles from './styles.js';

class GlossaryEditor extends React.Component {
  createObject = () => {
    const obj_class = this.props.objectClass;
    // populate a new empty object for the user
    const new_obj = glossaryUtils.getNewObjectTemplate(obj_class);
    this.props.onCreate(new_obj);
    this.props.setObjectEditorOpen(true);
    // return to whoever needs it
    return new_obj;
  }

  renderNewButton = () => {
    const { classes, t } = this.props;
    return (
      <Button 
        variant='contained' 
        color='primary'
        className={classes.newButton}
        onClick={this.createObject}
      >
        <Add className={classes.buttonIcon}/>
        {t('new')}
      </Button>
    );
  }

  closeObjectEditor = () => {
    this.props.setObjectEditorOpen(false);
  }

  render = () => {
    return (
      <Fragment>
        <PageTitle title={this.props.t('glossary editor')} />
        <Grid container spacing={2} data-test-id='GlossaryEditor'>
          <Grid item xs={12}></Grid>
          <Grid item xs={12}>
            <GlossarySearchResults renderNewButton={this.renderNewButton} />
            <GlossaryObjectEditor createObject={this.createObject} handleClose={this.closeObjectEditor} />
          </Grid>
          {/* <Grid item md={5} xs={12}>
            <GlossaryObjectEditor createObject={this.createObject} handleClose={this.closeObjectEditor} />
          </Grid> */}
        </Grid>
      </Fragment>
    );
  }
}

GlossaryEditor.propTypes = {
  t: PropTypes.func,
  objectClass: PropTypes.string,
  currentObject: PropTypes.object,
  isChangesSaved: PropTypes.bool,
};

const mapStateToProps = (state) => {
  return {
    objectClass: state.glossary.objectSearchClass,
    currentObject: state.glossary.currentObject,
    isChangesSaved: state.glossary.isObjectEditsSaved,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onCreate: (obj) => dispatch(currentObjectChangeHandler(obj)),
    setObjectEditorOpen: (isOpen) => dispatch(setObjectEditorOpen(isOpen)),
  };
};

export default compose(
  withTranslation(),
  connect(mapStateToProps, mapDispatchToProps),
  withStyles(themeToStyles),
)(GlossaryEditor);