import React, { Fragment } from 'react';
import { Grid, Typography, Divider } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import styles from './styles.js';

const PageTitle = props => {
  const { title, pageActionButton, classes } = props;
  return (
    <Fragment>
      { title &&
      <Grid className={classes.container} container justify='space-between'>
        <Typography className={classes.pageTitle} component='span' variant='h3'>{title}</Typography>
        { pageActionButton }
      </Grid>
      }
      { title &&
      <Divider className={classes.pageTitleDivider} />
      }
    </Fragment>

  );
};

PageTitle.propTypes = {
  title: PropTypes.string,
  pageActionButton: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.element,
  ]),
};

export default withStyles(styles, { withTheme: true })(PageTitle);
