import React, {Fragment} from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { compose } from 'redux';
import {connect} from 'react-redux';
import {withTranslation} from 'react-i18next';
import _ from 'lodash';
import Popover from '@material-ui/core/Popover';
import { withStyles } from '@material-ui/core/styles';
import InfoIcon from '@material-ui/icons/Info';
import { FormControl, InputLabel, List, ListItem, ListItemText, Paper } from '@material-ui/core';

import CONSTANT from '@hubcitymedia/commons-scripts/src/utils/globalConstants.js';
import {objectSearchClassChangeHandler} from 'src/store/actions/glossary.actions.js';
import styles from './styles';

function getOptions() {
  // We decided to not show requestable item bundles because those should be edited through the AR bundle editor GUI.
  return _.difference(CONSTANT.GLOSSARY_CLASSES, ['requestable-item-bundle']);
}

class GlossarySearchInput extends React.Component {
  state = {
    anchorEl: null,
  };
  componentDidMount = () => {
    // trigger the glossary object data to be fetched
    this.props.onSearch(this.props.objectClass);
  }
  changeSelect = (value) => {
    this.props.onChangeObjectClass(value);
  }
  handlePopoverOpen = (event) => {
    this.setState({
      anchorEl: event.currentTarget,
    });
  }
  handlePopoverClose = () => {
    this.setState({
      anchorEl: null,
    });
  }
  renderInfoContent = () => {
    // render the glossary info description text
    const glossary_class_contents = CONSTANT.GLOSSARY_CLASSES.map((glossary_class) => {
      if (glossary_class !== 'requestable-item-bundle') {
        return (
          <Fragment {...{
            key: glossary_class,
          }}>
            <div>
              <b>{glossary_class + ': '}</b>{this.props.t(`glossary-class-info.${glossary_class}`)}
            </div>
          </Fragment>
        );
      } else return;
    });
    return glossary_class_contents;
  }
  renderInfo = () => {
    // renders both the info button and the popover text that appears when you hover on the info button
    const {classes} = this.props;
    const is_open = Boolean(this.state.anchorEl);
    return (
      <Fragment>
        <InfoIcon {...{
          'aria-owns': open ? 'mouse-over-popover' : undefined,
          'aria-haspopup': true,
          onMouseEnter: this.handlePopoverOpen,
          onMouseLeave: this.handlePopoverClose,
        }}/>
        <Popover
          id="mouse-over-popover"
          className={classes.popover}
          classes={{
            paper: classes.paper,
          }}
          open={is_open}
          anchorEl={this.state.anchorEl}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          onClose={this.handlePopoverClose}
          disableRestoreFocus
        >
          {this.renderInfoContent()}
        </Popover>
      </Fragment>
    );
  }
  render = () => {
    const {classes} = this.props;
    return (
      <Paper className={classes.glossarySearchInputPaper}>
        <Fragment>
          <InputLabel className={classes.listLabel}>Object Class</InputLabel>
          <FormControl className={classes.selectList}>
            <List {...{
              name: 'glossarySearchInput',
              value: this.props.objectClass,
            }}>
              {getOptions().map(option => {

                return (
                  <ListItem 
                    button 
                    onClick={event => this.changeSelect(option)}
                    className={clsx(classes.listItem, this.props.objectClass === option && classes.listItemSelected)}
                    key={option}
                  >
                    <ListItemText className={classes.listText}>{option}</ListItemText>
                  </ListItem>
                );
              })}
            </List>
          </FormControl>
        </Fragment>
      </Paper>
    );
  }
}

GlossarySearchInput.propTypes = {
  onSearch: PropTypes.func,
  onChangeObjectClass: PropTypes.func,
  objectClass: PropTypes.string.isRequired,
};

const mapStateToProps = (state) => {
  return {
    objectClass: state.glossary.objectSearchClass,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onChangeObjectClass: (new_class) => dispatch(objectSearchClassChangeHandler(new_class)),
    onSearch: (new_class) => dispatch(objectSearchClassChangeHandler(new_class)),
  };
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(styles)
)(GlossarySearchInput);
