import React, {Component} from 'react';
import { connect } from 'react-redux';
import {withTranslation} from 'react-i18next';
import Notifier from 'src/utils/Notifier.js';

import { checkIfAuthenticated, logoutUser } from 'src/store/actions/auth.actions.js';
import Layout from 'src/components/Layout/Layout.js';

class App extends Component {
  render() {
    return (
      <div className='App'>
        <Notifier />
        <Layout />
      </div>
    );
  }
}

const mapStateToProps = state => {
  const {
    auth: { isLoggedIn },
  } = state;

  return {
    isLoggedIn,
  };
};

export default withTranslation()(connect(mapStateToProps, { checkIfAuthenticated, logoutUser })(App));
