import {AUTH} from 'src/store/actionTypes.js';
import httpService from 'src/utils/httpService.js';
import constants from 'src/utils/constants.js';

export const getAuthenticationModule = () => dispatch => {
  return httpService.get(constants.IDM_CONTEXT + '/info/uiconfig', {
    headers: {
      'X-OpenIDM-Username': 'anonymous',
      'X-OpenIDM-Password': 'anonymous',
      'X-OpenIDM-NoSession': 'true',
    },
  }).then(({ data }) => {
    const { configuration } = data;
    const oauthClientAuth = configuration.amDataEndpoints;
    if (oauthClientAuth) {
      // TODO: implement a new action creator to handle SSO authentication
      dispatch(authenticationModuleFailure());
    } else {
      dispatch(setAuthenticationModule('MANAGED_USER'));
    }
  }).catch(error => {
    dispatch(authenticationModuleFailure(error));
  });
};

export const setAuthenticationModule = authModule => {
  return {
    type: AUTH.FETCH_AUTH_MODULE_SUCCESS,
    payload: authModule,
  };
};

export const authenticationModuleFailure = response => {
  const { code, message } = response;
  return {
    type: AUTH.FETCH_AUTH_MODULE_FAILURE,
    payload: {
      code,
      message,
    },
  };
};

export const checkIfAuthenticated = () => dispatch =>  {
  return httpService.get(constants.IDM_CONTEXT + '/info/login')
    .then(({ data }) => {
      dispatch(authenticationSuccess(data));
    }).catch(() => {
      dispatch(logoutUser());
    });
};

export const authenticateUser = (userName, password) => dispatch => {
  return httpService.post(constants.IDM_CONTEXT + '/authentication?_action=login', {}, {
    headers: {
      'X-OpenIDM-NoSession': 'false',
      'X-OpenIDM-Password': password,
      'X-OpenIDM-Username': userName,
    },
  }).then(({ data }) => {
    dispatch(authenticationSuccess(data));
  }).catch(error => {
    dispatch(authenticationFailure(error));
  });
};

export const authenticationSuccess = response => {
  const { authenticationId, authorization } = response;
  return {
    type: AUTH.AUTHENTICATION_SUCCESS,
    payload: {
      authenticationId,
      authorization,
    },
  };
};

export const authenticationFailure = response => {
  const { code, message } = response;
  return {
    type: AUTH.AUTHENTICATION_FAILURE,
    payload: {
      code,
      message,
    },
  };
};

export const logoutUser = () => dispatch => {
  return httpService.post(constants.IDM_CONTEXT + '/authentication?_action=logout', {}, {
    headers: {
      'Content-Type': 'application/json',
      'X-OpenIDM-NoSession': 'true',
      'X-OpenIDM-Password': 'anonymous',
      'X-OpenIDM-Username': 'anonymous',
    },
  }).then(({ data }) => {
    dispatch(logoutSuccess(data));
  }).catch(error => {
    dispatch(logoutFailure(error));
  });
};

export const logoutSuccess = response => {
  return {
    type: AUTH.LOGOUT_SUCCESS,
    payload: response,
  };
};

export const logoutFailure = response => {
  const { code, message } = response;
  return {
    type: AUTH.LOGOUT_FAILURE,
    payload: {
      code,
      message,
    },
  };
};
