import is from 'check-types';
import _ from 'lodash';

import config from '@hubcitymedia/commons-scripts/src/utils/clientConfig.js';
import http from 'src/utils/httpService.js';
import {GENERIC} from 'src/store/actionTypes.js';
import constants from 'src/utils/constants.js';

export const updateManagedObjectTypes = () => (dispatch) => {
  return http.get(constants.IDM_CONTEXT + '/commons/config').then((response) => {
    const config_object = response.data.result;
    const managed_object_types = _.keys(config_object['managedObjectConfig']);
    return dispatch(changeManagedObjectTypes(managed_object_types));
  }).catch(error => {
    return dispatch(httpFailure(error, 'error'));
  });
};

export const changeManagedObjectTypes = (types) => ({
  type: GENERIC.CHANGE_MANAGED_OBJECT_TYPES,
  payload: types,
});

// You should probably be using 'alert' action below instead.  Feel free to export this if you need the customization.
const enqueueSnackbar = (notification) => {
  const key = notification.options && notification.options.key;
  return {
    type: GENERIC.ENQUEUE_SNACKBAR,
    notification: {
      ...notification,
      key: key || new Date().getTime() + Math.random(),
    },
  };
};

export const closeSnackbar = key => ({
  type: GENERIC.CLOSE_SNACKBAR,
  dismissAll: !key, // dismiss all if no key has been defined
  key,
});

export const removeSnackbar = key => ({
  type: GENERIC.REMOVE_SNACKBAR,
  key,
});

/*
Expose an 'alert' function for alerting things like success and failure to the user with a message.

This points to whichever alert/toast library we choose.
*/
export const alert = (message, level) => (dispatch) => {
  const level_to_options = config.alert.levelToOptions;
  const options = level_to_options[level];
  dispatch(enqueueSnackbar({
    message,
    options,
  }));
};

const getPayload = (response) => {
  if (!response.response) {
    const message = response;
    return {message};
  }
  else {
    const data = response.response.data;
    const is_data_populated = !is.null(data);
    const code = is_data_populated ? data.code : response.response.status;
    const message = is_data_populated ? data.message : response.response.statusText;
    return {code, message};
  }
};

export const httpFailure = (response, alert_level=false) => (dispatch) => {
  const payload = getPayload(response);
  if (alert_level) {
    dispatch(alert(payload.message, alert_level));
  }
  return {
    type: 'FAIL',
    payload: payload,
  };
};
