import React from 'react';
import ReactSelect from 'react-select';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Paper, TextField, Chip, MenuItem, Typography } from '@material-ui/core';
import { Cancel } from '@material-ui/icons';

import __ from '@hubcitymedia/commons-scripts/src/utils/jsUtils.js';


import themeToStyles from './styles.js';
const useStyles = makeStyles(themeToStyles);


function validateValue(value) {
  if (!value) {
    return;
  }
  value.forEach((item) => {
    if (!item.hasOwnProperty('value')) {
      __.requestError('The multi select item has no key called "value". That key is mandatory and must have a unique value.', 500);
    }
    if (!item.hasOwnProperty('label')) {
      __.requestError('The multi select item has no key called "label". That key is mandatory.', 500);
    }
  });
}

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />;
}

inputComponent.propTypes = {
  inputRef: PropTypes.string,
};

const components = {
  Control: (props) => {
    const {
      children,
      innerProps,
      innerRef,
      selectProps: { classes, TextFieldProps },
    } = props;

    return (
      <TextField
        fullWidth
        InputProps={{
          inputComponent,
          inputProps: {
            className: classes.input,
            ref: innerRef,
            children,
            ...innerProps,
          },
        }}
        {...TextFieldProps}
      />
    );
  },
  Menu: (props) => {
    // If user has clicked a Chip element, prevent the options menu from displaying
    if (event && event.type === 'mousedown' && event.target.className === 'MuiChip-label') {
      return null;
    }
    return (
      <Paper square className={props.selectProps.classes.paper} {...props.innerProps}>
        {props.children}
      </Paper>
    );
  },
  MultiValue: (props) => {
    return (
      <Chip
        tabIndex={-1}
        id={props.data}
        label={props.children}
        className={clsx(props.selectProps.classes.chip, {
          [props.selectProps.classes.chipFocused]: props.isFocused,
        })}
        onDelete={props.removeProps.onClick}
        deleteIcon={<Cancel {...props.removeProps} />}
      />
    );
  },
  NoOptionsMessage: (props) => {
    return (
      <Typography
        color="textSecondary"
        className={props.selectProps.classes.noOptionsMessage}
        {...props.innerProps}
      >
        {props.children}
      </Typography>
    );
  },
  Option: (props) => {
    return (
      <MenuItem
        ref={props.innerRef}
        selected={props.isFocused}
        component="div"
        style={{
          fontWeight: props.isSelected ? 500 : 400,
        }}
        {...props.innerProps}
      >
        {props.children}
      </MenuItem>
    );
  },
};

const MultiSelectField = props => {
  const [options, setOptions] = React.useState([]);

  function onInputChange(value, evt) {
    if (evt.action === 'input-change') {
      if (props.selectionLimit && props.value.length) {
        return '';
      }
      if (value) {
        props.queryInput(value)
          .then(action => {
            setOptions(action.payload);
          });
      } else {
        setOptions([]);
      }
    } else {
      if (evt.action === 'set-value') {
        setOptions([]);
      }
      props.onChange(value, evt);
    }
  }

  const classes = useStyles();
  const theme = useTheme();

  const selectStyles = {
    input: base => ({
      ...base,
      color: theme.palette.text.primary,
      '& input': {
        font: 'inherit',
      },
    }),
  };

  const {
    label,
    placeholder,
    type,
    onBlur,
    onChange,
    name,
    value,
    error,
    touched,
    isDisabled,
    isMulti,
  } = props;

  validateValue(value);

  return (
    <ReactSelect
      className={classes.reactSelectContainer}
      classes={classes}
      styles={selectStyles}
      inputId={name}
      type={type}
      name={name}
      isDisabled={isDisabled}
      TextFieldProps={{
        label,
        InputLabelProps: {
          htmlFor: {name},
          shrink: true,
        },
        placeholder,
        error: Boolean(touched && error),
        helperText: touched && error ? error : null,
      }}
      options={props.options || options}
      components={components}
      value={value}
      onBlur={onBlur}
      onInputChange={props.onInputChange || onInputChange}
      onChange={onChange}
      isMulti={isMulti}
    />
  );
};

inputComponent.propTypes = {
  innerRef: PropTypes.string,
};

components.Control.propTypes = {
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  selectProps: PropTypes.object,
};

components.Menu.propTypes = {
  selectProps: PropTypes.object,
  innerProps: PropTypes.object,
};

components.MultiValue.propTypes = {
  selectProps: PropTypes.object,
  isFocused: PropTypes.bool,
  removeProps: PropTypes.object,
};

components.NoOptionsMessage.propTypes = {
  innerProps: PropTypes.object,
  selectProps: PropTypes.object,
};

components.Option.propTypes = {
  innerProps: PropTypes.object,
  innerRef: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  isFocused: PropTypes.bool,
  isSelected: PropTypes.bool,
};

MultiSelectField.propTypes = {
  isFocused: PropTypes.bool,
  isSelected: PropTypes.bool,
  touched: PropTypes.bool,
  onBlur: PropTypes.func,
  queryInput: PropTypes.func,
  onChange: PropTypes.func,
  onInputChange: PropTypes.func,
  inputRef: PropTypes.func,
  innerRef: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
  innerProps: PropTypes.object,
  selectProps: PropTypes.object,
  removeProps: PropTypes.object,
  label: PropTypes.string,
  placeholder: PropTypes.string,
  name: PropTypes.string,
  type: PropTypes.string,
  value: PropTypes.array,
  error: PropTypes.string,
  objectType: PropTypes.string,
  options: PropTypes.array,
  selectionLimit: PropTypes.number,
  isMulti: PropTypes.bool,
};

export default compose(
  connect(null, null),
)(MultiSelectField);


