import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { compose } from 'redux';
import {connect} from 'react-redux';
import {withTranslation} from 'react-i18next';
import _ from 'lodash';
import 'core-js/features/set';
import { withStyles } from '@material-ui/core/styles';
import {
  Dialog,
  DialogTitle,
  IconButton,
  Typography,
  withWidth,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import __ from '@hubcitymedia/commons-scripts/src/utils/jsUtils';
import commonUtils from '@hubcitymedia/commons-scripts/src/utils/commonUtils.js';
import glossaryConstraints from '@hubcitymedia/commons-scripts/src/utils/glossaryConstraints.js';
import validate from '@hubcitymedia/commons-scripts/src/utils/glossaryValidators.js';
import glossaryUtils from '@hubcitymedia/commons-scripts/src/utils/glossaryUtils.js';

import {onSaveObjectHandler, onDeleteObjectHandler, currentObjectChangeHandler, objectSavedStatusChangeHandler} from 'src/store/actions/glossary.actions.js';
import {sortKeys} from 'src/utils/glossaryUtils.js';
import ObjectEditor from 'src/components/ObjectEditor';
import ConfirmationDialog from 'src/components/ConfirmationDialog';

import themeToStyles from './styles.js';


// keys that we never want to show in the object editor
const HIDDEN_KEYS = [
  '_id',
  '_rev',
  'class',
  'constraints',
];

function getKeysForEditor(object) {
  // Get all keys from object that we want to show in the editor.
  const own_keys = new Set(_.keys(object));
  const visible_keys = own_keys.difference(new Set(HIDDEN_KEYS));
  return visible_keys;
}

/**
 * Determines if the value in a key-value pair should be disabled.
 */
function isDisabled(key) {
  return (key === 'class');
}

function isTypeDisabled(key, object) {
  // the type is disabled for disabled inputs and for keys with types specified in the glossary constraints
  return isDisabled(key, object) ||
      commonUtils.getConstrainedKeysOfGlossaryObject(object).includes(key);
}

function isDropdown(key, object) {
  return (!isDisabled(key)) && glossaryUtils.doesKeyHaveFiniteValues(key, object['class']);
}

function getValuesForKey(key, object) {
  const value = object[key];
  return isDropdown(key, object) ? glossaryUtils.getFiniteValuesForKey(key, object['class']) : [value];
}

function validateObject(object) {
  if (!_.isEmpty(object)) {
    validate.class(object['class']);
  }
}

function getDescription(t, key) {
  // get key description, if it exists
  const string = 'glossary.description.' + key;
  const description = t(string);
  if (string === description) {
    return null;
  }
  else {
    return description;
  }
}

function getValidationMessage() {
  return '';
}

function isKeyDeletable(key, obj) {
  // it's a deletable key if and only if it's *not* a required key
  validate.class(obj.class);
  const req_keys = validate.getRequiredKeys(obj.class);
  const is_read_only = _.includes(req_keys, key);
  const is_deletable = !is_read_only;
  return is_deletable;
}

class GlossaryObjectEditor extends React.Component {
  state = {
    isConfirmationDialogOpen: false,
    glossaryObjectToDelete: null,
  }
  modifiedFormObject = (form_object) => {
    // include hidden pairs from the original object
    for (const hidden_key of HIDDEN_KEYS) {
      if (!form_object.hasOwnProperty(hidden_key)) {
        form_object[hidden_key] = this.props.currentObject[hidden_key];
      }
    }
    // convert user-inputted values to their appropriate types
    form_object = glossaryUtils.typeCastGlossaryObjectValues(form_object);
    return form_object;
  }
  saveObject = (form_object, form_constraints_object) => {
    // attach the types back onto the object
    const updated_object = form_object;
    updated_object['constraints'] = __.extendedDeep(updated_object['constraints'], form_constraints_object);
    // save to server
    this.props.onSave(updated_object);
  }
  updateObjectLocally = (form_object, form_constraints_object) => {
    // attach the types back onto the object
    const updated_object = form_object;
    updated_object['constraints'] = __.extendedDeep(updated_object['constraints'], form_constraints_object);
    // save to server
    this.props.onUpdateLocally(updated_object);
  }
  deleteObject = () => {
    const form_object = this.state.glossaryObjectToDelete;
    // get the id
    const obj_id = form_object['_id'];
    // send the delete command
    this.props.onDelete(obj_id);
  }
  getConstraintsObjectForObjectEditor = () => {
    // get the constraints object to give to the object editor.  requires some prepping
    const object = _.cloneDeep(this.props.currentObject);
    const constraints_object = object.constraints;
    if (!constraints_object) {
      __.requestError('constraints object is undefined. the glossary objects fed in must be invalid.');
    }
    // add in the builtin constraints
    const custom_constraints_object = __.extended(constraints_object, glossaryConstraints[object['class']]);
    return custom_constraints_object;
  }
  handleObjectChange = () => {
    // react to an object change
    this.props.onObjectStatusChange(false);
  }
  confirmDeleteObject = (form_object) => {
    // remember what they want to delete
    this.setState({glossaryObjectToDelete: form_object});
    // confirm that they really want to delete
    // cause the confirmation dialog to appear
    this.setState({isConfirmationDialogOpen: true});
  }
  closeDialog = () => {
    this.setState({isConfirmationDialogOpen: false});
  }
  render = () => {
    const { objectEditorIsOpen, handleClose, t, width } = this.props;
    const object = _.cloneDeep(this.props.currentObject);
    const objectEditorTitle = object._id ? 
      t('edit glossary object', { displayName: object.displayName }) : 
      t('new') + ' ' + this.props.objectClass;
    return (
      <Fragment>
        <ConfirmationDialog {...{
          dialogProps: {
            // is open prop
            open: this.state.isConfirmationDialogOpen,
            // accessability
            'aria-labelledby': t('confirm-delete-glossary-object-dialog-aria-labelledby'),
            'aria-describedby': t('confirm-delete-glossary-object-dialog-aria-describedby'),
          },
          message: t('confirm-glossary-object-deletion'),
          onClickOk: this.deleteObject,
          onClose: this.closeDialog,
        }}/>
        <Dialog 
          maxWidth='lg'
          fullScreen={width === 'sm' || width === 'xs'}
          fullWidth={true}
          scroll='body'
          PaperProps={{
            className: 'appBarShift',
          }}
          onClose={handleClose}
          open={objectEditorIsOpen}
          aria-labelledby={objectEditorTitle}
        >
          <DialogTitle id={objectEditorTitle} disableTypography>
            <Typography variant='h6'>{objectEditorTitle}</Typography>
            <IconButton aria-label={t('label-close')} className={this.props.classes.closeButton} onClick={handleClose}>
              <CloseIcon />
            </IconButton>
          </DialogTitle>
          <ObjectEditor {...{
            // title: objectEditorTitle,
            handleClose,
            getDescription,
            object,
            objectType: 'object',
            enableConstraints: true,
            constraintsObject: this.getConstraintsObjectForObjectEditor(),
            getValidationMessage,
            getKeysForEditor,
            getValuesForKey,
            sortKeys,
            isKeyDeletable,
            isDisabled,
            isTypeDisabled,
            isDropdown,
            validateObject,
            // form object modifier before actions
            modifiedFormObject: this.modifiedFormObject,
            // button actions
            onNew: this.props.createObject,
            onSave: this.saveObject,
            onDelete: this.confirmDeleteObject,
            onChange: this.handleObjectChange,
            onUpdateLocally: this.updateObjectLocally,
            // key buttons
            onDeleteKey: this.updateObjectLocally,
            onCreateKey: this.updateObjectLocally,
          }} />
          {/* <div {...{
            className: clsx([
              this.props.classes['save-status-wrapper'],
              this.props.isChangesSaved && this.props.classes['saved'],
              (!this.props.isChangesSaved) && this.props.classes['unsaved'],
            ]),
          }}>
            {this.props.isChangesSaved ? this.props.t('changes-saved') : this.props.t('changes-not-saved')}
          </div> */}
        </Dialog>
      </Fragment>
    );
  }
}

GlossaryObjectEditor.propTypes = {
  currentObject: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
  return {
    objectClass: state.glossary.objectSearchClass,
    currentObject: state.glossary.currentObject,
    isChangesSaved: state.glossary.isObjectEditsSaved,
    objectEditorIsOpen: state.glossary.isObjectEditorOpen,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onCreate: (obj) => dispatch(currentObjectChangeHandler(obj)),
    onSave: (obj) => dispatch(onSaveObjectHandler(obj)),
    onObjectStatusChange: (status) => dispatch(objectSavedStatusChangeHandler(status)),
    onDelete: (id) => dispatch(onDeleteObjectHandler(id)),
    onUpdateLocally: (obj) => dispatch(currentObjectChangeHandler(obj)),
  };
};

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withTranslation(),
  withStyles(themeToStyles),
  withWidth(),
)(GlossaryObjectEditor);
