// polyfill imports must come BEFORE all other imports.  support IE 11.
import 'react-app-polyfill/ie11';
import React from 'react';
import ReactDOM from 'react-dom';
import { Router } from 'react-router-dom';
import { createBrowserHistory } from 'history';
import {Provider} from 'react-redux';
import { MuiThemeProvider } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import 'typeface-roboto';
import { SnackbarProvider } from 'notistack';

import config from '@hubcitymedia/commons-scripts/src/utils/clientConfig.js';

import httpService from './utils/httpService.js';
import './utils/i18n.js';
import './styles/index.scss';
import theme from './styles/theme.js';
import App from './App.js';
import * as serviceWorker from './serviceWorker.js';
import store from './store';
import { getAuthenticationModule, checkIfAuthenticated } from './store/actions/auth.actions.js';

const history = createBrowserHistory();

httpService.setupInterceptors(store, history);

store.dispatch(getAuthenticationModule());
store.dispatch(checkIfAuthenticated());

// add action to all snackbars
const notistackRef = React.createRef();
const onClickDismiss = key => () => {
  notistackRef.current.closeSnackbar(key);
};

const arApp = (
  <Provider store={store}>
    <Router history={history}>
      <MuiThemeProvider theme={theme}>
        <SnackbarProvider {...{
          ...config.alert.baseProps,
          ref: notistackRef,
          action: (key) => (
            <IconButton key="close" aria-label="close" color="inherit" onClick={onClickDismiss(key)}>
              <CloseIcon style={{
                fontSize: 20,
              }} />
            </IconButton>
          ),
        }}>
          {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
          <CssBaseline />
          <App />
        </SnackbarProvider>
      </MuiThemeProvider>
    </Router>
  </Provider>
);

const authCallback = function() {
  ReactDOM.render(arApp, document.getElementById('root'));
};

httpService.getUiConfig(store, authCallback);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
